<script>
import axios from "axios";

export default {
  name: "list2",
  data() {
    return {
      links: "http://www.baidu.com",
      states: "",
      loading: false,
      dialogVisible: false,
      state: "",
      data3: "", //
      info: [],
      CX: {
        list1: "",
        list2: "",
        list3: "",
        list4: "",
      },
      total: 0,
      pageSize: 10, //
      pageSizes: [5, 10, 15, 20, 50], //
      currentPage: 1, //
      pageCount: 7, //
    };
  },
  //
  methods: {
    async getAllSchool() {
      let arr = [];
      await this.axios({
        method: "get",
        url: "api/students/getdata",
        params: {
          account: this.$cookies.get("user_name"),
          action: "list_college",
        },
      }).then((resp) => {
        arr = resp.data.college_name;
      });

      return arr;
    },
    querySearchAsync(queryString, cb) {
      this.getAllSchool().then((school_arr) => {
        let school = school_arr;
        let results = queryString
          ? school.filter(this.createStateFilter(queryString))
          : school.slice(0, 30);
        clearTimeout(this.timeout);
        console.log(results);
        this.timeout = setTimeout(() => {
          // cb(results);
          console.log(cb(results));
        }, 3000 * Math.random());
      });
    },
    createStateFilter(queryString) {
      return (state) => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },

    // 
    handleSelect(item) {
      console.log("@@", item);
    },

    get() {
      if (this.states === "") {
        this.$message.error("");
      } else {
        axios({
          method: "POST",
          url: "api/students/selectdata",
          data: {
            action: "select_college",
            college_address: "",
            college_name: this.states,
            college_type: [],
            account: this.$cookies.get("user_name"),
          },
        }).then((resp) => {
          console.log(resp.data);
          this.info = resp.data.data;
          this.total = resp.data.length;
          this.$message.success("");
        });
      }
    },
    //
    get2() {
      if (this.CX.list1 == "") {
        this.$message.error("");
      } else if (this.CX.list2 == "") {
        this.$message.error("");
      } else if (this.CX.list3 == "") {
        this.$message.error("");
      } else {
        axios({
          method: "POST",
          url: "api/students/selectdata",
          data: {
            action: "select_college",
            college_address: this.CX.list1,
            college_name: "",
            college_type: [this.CX.list2, this.CX.list3],
            account: this.$cookies.get("user_name"),
          },
        })
          .then((resp) => {
            // console.log(resp.data.data)
            this.info = resp.data.data;
            this.total = resp.data.length;
            this.$message.success("");
          })
          .catch(() => {
            console.log(this.CX.list1);
            console.log(this.CX.list2);
          });
      }
    },
    //
    get3() {
      axios({
        method: "POST",
        url: "api/students/selectdata",
        data: {
          action: "select_college",
          college_address: "",
          college_name: "",
          college_type: [],
          account: this.$cookies.get("user_name"),
        },
      })
        .then((resp) => {
          this.info = resp.data.data;
          this.total = resp.data.length;
        })
        .catch(() => {
          console.log(this.CX.list1);
          console.log(this.CX.list2);
        });
    },
    //
    petImage(name) {
      return "api/students/jpeg/" + name;
    },
    collect(id) {
      axios({
        method: "POST",
        url: "api/students/collectdata",
        data: {
          action: "collect_college",
          college_id: id,
          account: this.$cookies.get("user_name"),
          // account:'12345678'
        },
      }).then((resp) => {
        console.log(resp.data);
        if (resp.data.ret === 0) {
          this.$message.success("");
        }
        if (resp.data.ret === 1) {
          this.$message.error("");
        }
      });
    },
  },

  //
  created() {
    this.get3();
  },
};
</script>

